import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SimTest, selectedListModel, AddQuestions, AddChoices } from 'src/app/Models/CommonModels';
import { CommonServiceService } from 'src/app/common-service.service';

@Component({
  selector: 'app-assign-students-dialog',
  templateUrl: './assign-students-dialog.component.html',
  styleUrls: ['./assign-students-dialog.component.css']
})
export class AssignStudentsDialogComponent implements OnInit {
  displayedColumns: string[] = ['sNo','firstName','emailId'];
  dialogType: string;
  userSession = JSON.parse(localStorage.getItem('userSession'));
  newStudentslist: selectedListModel[];
  selectedStudentList: selectedListModel[];
  checkedIDs;
  questionsList: AddQuestions[] =[];
  choice;
  correctChiceSelect;
  testId: number;
  addStuBtn= true;
  noStudenAssign = false;
  constructor(private commonServiceService: CommonServiceService, public dialogRef: MatDialogRef<AssignStudentsDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  addTestform : FormGroup = new FormGroup ({
    testName: new FormControl('', [Validators.required]),
    duration: new FormControl('', [Validators.required]),
    passPercentage: new FormControl('', [Validators.required])
  });

  addTestQuestions : FormGroup = new FormGroup ({
    Question: new FormControl('', [Validators.required]),
    ChoiceAnswer1: new FormControl('', [Validators.required]),
    ChoiceAnswer2: new FormControl('', [Validators.required]),
    ChoiceAnswer3: new FormControl('', [Validators.required]),
    ChoiceAnswer4: new FormControl('', [Validators.required]),
    correctChoice: new FormControl('', [Validators.required])
  });
  ngOnInit(): void {
    this.dialogType = this.data.DialogType;
    if(this.dialogType == 'GetAssignStudents') {
      this.testId = this.data.testId;
      this.GetAssignedStudentDetails();
    }
    if(this.dialogType == 'AddAssignStudents') {
      this.testId = this.data.testId;
      this.getStudentetails();
    }
    if(this.dialogType == "AddQuestion")  {
      this.testId = this.data.testId;
    }
  }

  addTestSubmit() {
    if(this.addTestform.valid) {
      let simTest: SimTest = {
        TestId : 0,
        TestName : this.addTestform.value.testName,
        Duration : this.addTestform.value.duration,
        NoOfQuestions : 0,
        PassPercentage : this.addTestform.value.passPercentage,
        CustId: 1
      }
      this.commonServiceService.addTest(simTest).subscribe(data=>{
        if(data) {
          this.dialogRef.close();
        }
      })

    }
  }

  getStudentetails(){
    this.commonServiceService.GetStudentDetails(this.testId).subscribe(data=>{
      if(data) {
        this.newStudentslist = data;
      }
    });
  }

  GetAssignedStudentDetails() {
    this.commonServiceService.GetAssignedStudentDetails(this.testId).subscribe(data=>{
      if(data) {
        this.selectedStudentList = data;
      }
    });
  }

  addQuestionsToList() {
    if(this.addTestQuestions.valid) {
      let ChoiceAnswerList = [];
      ChoiceAnswerList[0] = this.addTestQuestions.value.ChoiceAnswer1;
      ChoiceAnswerList[1] = this.addTestQuestions.value.ChoiceAnswer2;
      ChoiceAnswerList[2] = this.addTestQuestions.value.ChoiceAnswer3;
      ChoiceAnswerList[3] = this.addTestQuestions.value.ChoiceAnswer4;
      let choiceList : AddChoices[] = [];
      for(let i=0; i<4 ; i++) {
        let vl = 'ChoiceAnswer'+i;
        choiceList[i] = {
          ChoiceAnswer: ChoiceAnswerList[i],
          CorrectChoice: this.correctChiceSelect == (i+1) ? true: false
        }
      }
      let questionList: AddQuestions = {
        TestId : this.testId,
        Question: this.addTestQuestions.value.Question,
        Choices: choiceList
      };
      this.questionsList.push(questionList);
    }
  }

  saveAndNext() {
    if(this.addTestQuestions.valid) {
    this.addQuestionsToList();
    this.addTestQuestions.reset();
    }
  }

  saveAndSubmit() {
    this.addQuestionsToList(); 
    this.commonServiceService.AddQuestions(this.questionsList).subscribe(data=>{
      if(data) {
        this.dialogRef.close();
      }
    })
  }

  studentChecked(event: any, custId:number) {
    this.newStudentslist.find(x=>x.custId==custId).isChecked = event.checked;
  }

  AssignStudents() {
    let Ids = this.newStudentslist.filter(x=>x.isChecked == true).map(x=>x.custId);
    if(Ids.length>0){
      this.addStuBtn = false;
      let data:string[] = [];
      data.push(this.testId.toString());
      data.push(Ids.join(','));
        this.commonServiceService.AssignStudents(data).subscribe(data=>{
          this.dialogRef.close();
        })
      } else {
        this.noStudenAssign = true;
      }
  }

  // changeSelection() {
  //   this.fetchSelectedItems()
  // }

  // fetchSelectedItems() {
  //   this.selectedStudentList = this.newStudentslist.filter((value, index) => {
  //     return value.isChecked
  //   });
  // }

  // fetchCheckedIDs() {
  //   this.checkedIDs  = []
  //   this.newStudentslist.forEach((value, index) => {
  //     if (value.isChecked) {
  //       this.checkedIDs.push(value.custId);
  //     }
  //   });
  // }

}
