import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CommonServiceService } from 'src/app/common-service.service';

@Component({
  selector: 'app-view-questions',
  templateUrl: './view-questions.component.html',
  styleUrls: ['./view-questions.component.css']
})
export class ViewQuestionsComponent implements OnInit, OnDestroy {
  testPapers: any;
  AlphabetCollection: string[] = ['a', 'b', 'c', 'd'];
  TestId:Number;
  TestName:string;
  questionsLengthExists: boolean;
  constructor(private commonServiceService: CommonServiceService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.queryParams.subscribe(queryParams => {
      this.TestId = Number(queryParams['TestId']);
      this.TestName = queryParams['TestName'];
    });
    this.commonServiceService.GetTestPapersAdmin(this.TestId).subscribe(res => {
      if(res){
        this.testPapers = res;
        this.questionsLengthExists = this.testPapers.length>0? true: false;
      }
    });
  }

  ngOnDestroy() {
    this.TestId = null;
    this.TestName = null;
  }
}
