import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonServiceService } from '../common-service.service';

@Component({
  selector: 'app-student-dashboard',
  templateUrl: './student-dashboard.component.html',
  styleUrls: ['./student-dashboard.component.css']
})
export class StudentDashboardComponent implements OnInit {
  assignedTests: any;
  userSession: any;
  displayedColumns: string[] = ['testId', 'testName', 'noOfQuestions', 'duration', 'passPercentage', 'attempted', 'percentage', 'takeTest'];
  constructor(private commonServiceService: CommonServiceService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    if (this.commonServiceService.getUserSession() != null) {
      this.userSession = JSON.parse(localStorage.getItem('userSession'));
      this.getTestDetails();
    } else {
      this.router.navigate(['']);
    }
  }

  getTestDetails() {
    this.commonServiceService.GetAllUserTests(this.userSession.custId).subscribe(res => {
      if (res) {
        this.assignedTests = res;
      }
    });
  }

  takeTest(event) {
    this.router.navigate(['/testComponentComponent'], { queryParams: { TestId: event.testId, TestName: event.testName, duration: event.duration } });
  }


}