import { Component, OnInit, Input, EventEmitter, Output  } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as CryptoJs from 'crypto-js';
import { CustomerAccountsModel } from '../Models/CommonModels'
import { CommonServiceService } from '../common-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  signUpResponse: any ;
  showSpinner : boolean =true;
  invalidCred: boolean = false;
  logInBtnVisible = true;

  formLogin: FormGroup = new FormGroup ({
    email: new FormControl('', [Validators.required]),
    password: new FormControl('', [Validators.required])
  })


  constructor(private commonServiceService: CommonServiceService, private router: Router) { }

  ngOnInit(): void {
  }

  login(){
    if(this.formLogin.valid) {
      let EmailId = this.formLogin.value.email;
      let pwd = this.formLogin.value.password;
      this.logInBtnVisible = false;
      this.commonServiceService.logIn(EmailId,pwd).subscribe(data=>{
        if(data) {
          localStorage.setItem('userSession', JSON.stringify(data));
          this.commonServiceService.isVisibleSource.next(true);
          if(data.roleId == 1) {
            this.router.navigate(['/adminDashBoard']);
          }else if(data.roleId == 2) {
            this.router.navigate(['/studentDashBoard']);
          }
        } else {
          this.invalidCred = true;
          this.logInBtnVisible = true;
        }
      });

    }
  }
}
