import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminDashboardComponent } from './admin-dashboard/admin-dashboard.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { StudentDashboardComponent } from './student-dashboard/student-dashboard.component';
import { ViewQuestionsComponent } from './admin-dashboard/view-questions/view-questions.component';
import { TestComponentComponent } from './student-dashboard/test-component/test-component.component';


const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'},
  {path: 'signUp', component: SignupComponent},
  {path: 'adminDashBoard', component:  AdminDashboardComponent},
  {path: 'studentDashBoard', component:  StudentDashboardComponent},
  {path: 'adminViewQuestions', component:  ViewQuestionsComponent},
  {path: 'testComponentComponent', component:  TestComponentComponent}
  // {path: 'adminViewQuestions', component:  ViewQuestionsComponent, data : {some_data : 'some value'}}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
