import { Component, OnInit } from '@angular/core';
import { CommonServiceService } from '../common-service.service';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { AssignStudentsDialogComponent } from './assign-students-dialog/assign-students-dialog.component';

@Component({
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})
export class AdminDashboardComponent implements OnInit {
  displayedColumns: string[] = ['testId', 'testName', 'noOfQuestions', 'AddQuestions', 'AddStudents', 'ShowStudents', 'ViewQuestions', 'Delete'];
  dataSource;
  userSession;

  constructor(private route: ActivatedRoute, private commonServiceService: CommonServiceService, private router: Router, private dialog: MatDialog) { }
  userDetails: any;
  testId: number;

  ngOnInit(): void {
    if (this.commonServiceService.getUserSession() != null) {
      this.commonServiceService.logIn('admin', 'admin').subscribe(data => {
        if (data) {
          if (data.RoleId == 1) {
            this.userDetails = data;
          }
        }
      });
      this.getAllTests(1);
      this.testId = Number(this.route.snapshot.paramMap.get('id'));
    } else {
      this.router.navigate(['']);
    }
  }

  getAllTests(CustId: number) {
    this.commonServiceService.GetTests(CustId).subscribe(data => {
      if (data) {
        this.dataSource = data;
      }
    })
  }

  addStudentClick(event) {
    this.dialog.open(AssignStudentsDialogComponent,
      {
        width: '300px',
        data: {
          testId: event.testId,
          DialogType: 'AddAssignStudents'
        }
      });
  }

  AssignedStudents(event) {
    this.dialog.open(AssignStudentsDialogComponent,
      {
        width: '450px',
        data: {
          testId: event.testId,
          DialogType: 'GetAssignStudents'
        }
      });
  }

  addTest() {
    const dialogRef = this.dialog.open(AssignStudentsDialogComponent,
      {
        width: '500px',
        height: '400px',
        data: {
          DialogType: 'AddTest'
        }
      });
    dialogRef.afterClosed().subscribe(data =>
      this.getAllTests(1)
    );
  }

  addQuestions(event) {
    const dialogRef = this.dialog.open(AssignStudentsDialogComponent,
      {
        width: '80%',
        height: '70%',
        data: {
          testId: event.testId,
          DialogType: 'AddQuestion'
        }
      });
    dialogRef.afterClosed().subscribe(data =>
      this.getAllTests(1)
    );
  }

  deleteTest(TestId) {
    this.commonServiceService.DeleteTests(TestId).subscribe(res => {
      if (res) {
        this.getAllTests(1)
      }
    })
  }

  viewTestsQuestions(event) {
    this.router.navigate(['/adminViewQuestions'], { queryParams: { TestId: event.testId, TestName: event.testName } });
  }

}
