import { Component, OnInit, Input, EventEmitter, Output  } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CommonServiceService } from '../common-service.service';
import { Router } from '@angular/router';
import { CustomerAccountsModel } from '../Models/CommonModels';
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  
  CustomerAccountsSignUp;
  roleId: number = 2 ;
  emailExist = false;
  signUpbtn = true;
  mobNumberPattern = "^((\\+91-?)|0)?[0-9]{10}$"; 

  formSignUp: FormGroup = new FormGroup ({
    firstName: new FormControl('', [Validators.required]),
    lastName: new FormControl('', [Validators.required]),
    email: new FormControl('', [Validators.required, , Validators.email]),
    pwd: new FormControl('', [Validators.required]),
    roleId: new FormControl(''),
    number: new FormControl('', [Validators.required, Validators.pattern(this.mobNumberPattern)]),
  })
  constructor(private commonServiceService: CommonServiceService, private router: Router) { }

  ngOnInit(): void {
  }
  signUp(){
    if(this.formSignUp.valid) {
      this.signUpbtn = false;
      let CustomerAccounts: CustomerAccountsModel = {
        CustId : 0,
      FirstName : this.formSignUp.value.firstName,
      LastName : this.formSignUp.value.lastName,
      EmailId : this.formSignUp.value.email,
      Pwd : this.formSignUp.value.pwd,
      RoleId : this.formSignUp.value.roleId,
      Role : (this.formSignUp.value.roleId == 1 ? 'admin' : 'student'),
      Phone : this.formSignUp.value.number
      }
      this.CustomerAccountsSignUp = CustomerAccounts;
      this.commonServiceService.signUpUser(this.CustomerAccountsSignUp).subscribe(data => {
        if(data == 1) {
          // alert('SignUp Success');
          this.router.navigate(['']);
        }
         else if(data == 2) {
          this.emailExist = true;
          this.signUpbtn = true;
        }
      });
      
    }
  }
}
