import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonServiceService } from 'src/app/common-service.service';
import { AddQuestions, selectedListModel, TestChoiceModel } from 'src/app/Models/CommonModels';

@Component({
  selector: 'app-test-component',
  templateUrl: './test-component.component.html',
  styleUrls: ['./test-component.component.css']
})
export class TestComponentComponent implements OnInit {
  TestId: number;
  TestName: any;
  Duration: number;
  interval;
  testTimeLeft;
  testPapers: any;
  selectedAnsers: any = [];
  nullItem: any;
  finalChoices: any;
  testContent = true;
  submitDisable = false;

  constructor(private commonServiceService: CommonServiceService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    if (this.commonServiceService.getUserSession() != null) {
      this.route.queryParams.subscribe(queryParams => {
        this.TestId = Number(queryParams['TestId']);
        this.TestName = queryParams['TestName'];
        this.Duration = Number(queryParams['duration']);
      });
      this.getTestQuestions();
      this.startTimer(this.Duration * 60)
    } else {
      this.router.navigate(['']);
    }
  }

  getTestQuestions() {
    this.commonServiceService.GetTestQuestions(this.TestId).subscribe(data => {
      if (data) {
        this.testPapers = data;
        if (this.testPapers.length == 0) {
          this.testContent = false;
        }
      }
    });
  }

  startTimer(timeLeft) {
    this.interval = setInterval(() => {
      if (timeLeft > 0) {
        timeLeft--;
        this.testTimeLeft = this.getTimeInTimer(timeLeft);
      } else {
        this.testSubmit();
      }
    }, 1000)
  }

  getTimeInTimer(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor(d % 3600 / 60);
    var s = Math.floor(d % 3600 % 60);

    var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
    var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
    var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
    return hDisplay + mDisplay + sDisplay;
  }

  answerClick(choiceObj) {
    if (this.selectedAnsers.length > 0) {
      if (this.selectedAnsers.filter((item) => item.questionId === choiceObj.questionId).length > 0) {
        const index = this.selectedAnsers.indexOf(choiceObj);
        this.selectedAnsers.splice(index, 1);
      }
    }
    this.selectedAnsers.push(choiceObj);
  }

  testSubmit() {
    this.submitDisable = true;
    if (this.selectedAnsers.length == 0) {
      this.nullItem = this.testPapers[0].choices[0];
      this.nullItem.choiceAnswer = '';
      this.nullItem.choiceId = 0;
      this.selectedAnsers.push(this.nullItem);
    }

    let CustId = JSON.parse(localStorage.getItem('userSession')).custId;
    let finalChoices: TestChoiceModel = {
      custId: CustId,
      testId: this.TestId,
      choices: this.selectedAnsers
    }
    this.commonServiceService.GenerateTestResult(finalChoices).subscribe(data => {
      this.router.navigate(['/studentDashBoard']);
      if(data != 1) {
        this.submitDisable = false
      }
    });
  }
}
