import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { CustomerAccountsModel } from './Models/CommonModels';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type':'application/json; charset=utf-8'})
}

@Injectable({
  providedIn: 'root'
})
export class CommonServiceService {

  private apiEndPoint = environment.ApiPreLik;
  isVisibleSource: BehaviorSubject<boolean> = new BehaviorSubject(false);

  constructor(private httpClient: HttpClient) { }

  getUserSession(): Observable<boolean>{
    return JSON.parse(localStorage.getItem('userSession'));
  }

  getCustomers(): Observable<any>{
    return this.httpClient.get(this.apiEndPoint+'CustomerAccounts');
  }

  signUpUser(data): Observable<any>{
    return this.httpClient.post(this.apiEndPoint+'CustomerAccounts/RegisterUser',data, httpOptions);
  }

  logIn(email: string, password: string):  Observable<any>{
    return this.httpClient.get<CustomerAccountsModel>(this.apiEndPoint+'CustomerAccounts/Login/'+email+'/'+password);
  }

  addTest(data): Observable<any>{
    return this.httpClient.post(this.apiEndPoint+'Admin/CreateTest',data,httpOptions);
  }

  GetTests(CustId): Observable<any>{
    return this.httpClient.get<CustomerAccountsModel>(this.apiEndPoint+'Admin/GetTests/'+CustId);
  }

  DeleteTests(TestId): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Admin/DeleteTests/'+TestId);
  }

  GetStudentDetails(TestId): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Admin/GetStudentDetails/'+TestId);
  }

  GetAssignedStudentDetails(TestId): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Admin/GetAssignedStudentDetails/'+TestId);
  }

  GetAllUserTests(custId: Number): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Student/GetAllUserTests/'+custId);
  }

  GetTestPapersAdmin(TestId: Number): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Admin/GetTestPapersAdmin/'+TestId);
  }
  
  GetTestQuestions(TestId: Number): Observable<any>{
    return this.httpClient.get<any>(this.apiEndPoint+'Student/GetTestQuestions/'+TestId);
  }
  
  GenerateTestResult(data): Observable<any>{
    return this.httpClient.post(this.apiEndPoint+'Student/GenerateTestResult',data,httpOptions);
  }
  
  AddQuestions(data): Observable<any>{
    return this.httpClient.post(this.apiEndPoint+'Admin/AddQuestions',data,httpOptions);
  }
  
  AssignStudents(data): Observable<any>{
    return this.httpClient.post(this.apiEndPoint+'Admin/AssignStudents',data,httpOptions);
  }
}

